<template>
  <div class="wrap">
    <div class="container">
      <div class="packages-wrap">
        <div class="package-item" v-for="item in packages" :key="item.id">
          <div class="red-package" v-if="item.open">
            <span class="money-show">{{ item.money }}</span>
          </div>
          <div class="red-package un-open" v-else @click="open(item)">
            <div class="open-btn-wrap">
              <div class="open-btn" :class="item.rollback">￥</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <van-button color="linear-gradient(to right, #ff6034, #ee0a24)" style="padding-left:2rem;padding-right:2rem" size="small" round type="primary" @click="oneMore">再来一次</van-button>
      <van-button color="linear-gradient(to right, #ff6034, #ee0a24)" style="padding-left:2em;padding-right:2rem" size="small" round type="primary" @click="openAll">{{ isAllOpened ? "去收账" : "全开" }}</van-button>
      <van-button color="linear-gradient(to right, #ff6034, #ee0a24)" style="padding-left:2rem;padding-right:2rem" size="small" round type="primary" @click="qOpenAll" v-if="!isAllOpened">快速全开</van-button>
    </div>
  </div>
  <van-popup v-model:show="show" class="result-container">
    <div>{{ name }}获得的红包为</div>
    <div class="money-s">
      <span class="total">{{ totalMoney }}</span> 元</div>
  </van-popup>
</template>

<script>
export default {
  name: "HomeView",
  data() {
    return {
      packages: [],
      rollback: "",
      isAllOpened: false,
      show:false,
      name:"",
      totalMoney:0
    };
  },
  watch: {
    packages: {
      deep: true,
      handler(val) {
        let exits = val.filter((v) => v.open == true);
        this.isAllOpened = exits.length == this.packages.length;
      },
    },
  },
  created() {
    let { min, max, name, total } = this.$route.query;
    this.name = name;
    for (let i = 1; i <= total; i++) {
      this.packages.push({
        id: i,
        money: this.getRandom(min, max),
        open: false,
        rollback: "",
      });
    }
  },
  methods: {
    getRandom(a, b) {
      let min = a * 1, max = b * 1;
      if (min == max) {
        return min
      }
      return ((Math.random() * (max - min)).toFixed(2) * 1 + min).toFixed(2);
    },
    open(data) {
      data.rollback = "rollback";
      setTimeout(() => {
        data.open = true;
      }, 1000);
    },
    oneMore() {
      this.$router.go(-1)
    },
    qOpenAll(){
      this.packages.filter(v=>{return !v.open}).map((v, i) => {
        v.rollback = "rollback";
        setTimeout(() => {
          v.open = true;
        }, 1000);
      });
    },
    openAll() {
      if (this.isAllOpened) {
        let sum = 0;
        this.packages.map((v) => {
          sum += v.money * 1;
        });
        this.totalMoney = sum.toFixed(2).toString();
        this.show = true;
        return;
      }
      this.packages.filter(v=>{return !v.open}).map((v, i) => {
        setTimeout(() => {
          v.rollback = "rollback";
          setTimeout(() => {
            v.open = true;
          }, 1000);
        }, i * 1000);
      });
    },
  },
};
</script>
